@use '../../abstracts/variables/colors' as *;
@use '../../abstracts/variables/size' as *;
@use '../../abstracts/mixins/mixins' as *;

.header-nav-link {
	border: 1px solid transparent;
	color: $control-text;
	font-size: 14px;
	font-weight: 500;
	display: inline-block;
	padding: 8px 12px;
	text-decoration: none;

	&:hover {
		color: $primary-hover-color;
	}

	&.active {
		border: 1px solid $border-secondary-color;
		border-radius: 8px;
		color: $primary-hover-color;
	}

	&:focus {
		border: 1px solid $primary-hover-color;
		box-shadow: none;
		outline: none;
		border-radius: 8px;
	}	
}
