@use '../abstracts/placeholders/placeholders' as *;
@use '../abstracts/variables/colors' as *;
@use '../abstracts/variables/size' as *;

.wrap {

    form {

        .mycontainer {
            display: flex;
            flex-wrap: wrap;
            align-items: flex-start;
            gap: 20px;

            // Left
            .left-column {
                width: 320px;
                padding: 24px;
                border-radius: 16px;
                background: $light-bg;

                // Show Accessibility Statement
                .box-control-switch {
                    display: flex;
                    flex-wrap: wrap;
                    align-items: center;
                    gap: 12px;
                    border-bottom: 1px solid $border-color-light;
                    padding-bottom: 22px;
                    margin-bottom: 22px;

                    svg {
                        padding: 8px;
                        border-radius: 6px;
                        border: 1px solid $border-secondary-color;
                        background: $light-bg;
                        width: 16px;
                        height: 16px;
                    }

                    .box-swich {
                        position: relative;
                        width: calc(100% - 46px);

                        label.switch {
                            display: flex;
                            flex-wrap: wrap;
                            align-items: center;
                            width: auto;

                            @extend %toggle-switch;

                            .slider.round {
                                left: unset;
                                top: 4px;
                                right: 0;
                                width: 36px;
                                height: 20px;
                            }

                            .slider::before {
                                height: 16px;
                                width: 16px;
                                bottom: 2px;
                                left: 2px;
                            }

                            input:checked + .slider::before {
                                -webkit-transform: translateX(16px);
                                -ms-transform: translateX(16px);
                                transform: translateX(16px);
                            }
                        }
                    }
                }

                // Company Website
                .box-control-company-website {
                    position: relative;

                    .protocol {
                        display: flex;
                        flex-wrap: wrap;
                        align-items: center;
                        position: absolute;
                        color: $setting-description;
                        font-size: 16px;
                        font-style: normal;
                        font-weight: 400;
                        line-height: 24px;
                        padding: 10px 14px;
                        height: 30px;
                        border: none;
                        border-right: 1px solid $border-secondary-color;

                        &.focus {
                            border-right: 2px solid $primary-hover-color;
                        }
                    }

                    input {
                        padding-left: 92px;
                    }
                }

                // Confirmation Checkbox
                .box-control-confirm {
                    position: relative;

                    input {
                        margin: 6px 0 6px 0;
                        border: 1px solid $border-secondary-color;
                        border-radius: 6px;
                        width: 20px;
                        height: 20px;
                        margin-right: 12px;
                        position: absolute;
                        top: 0;
                        left: 0;

                        &::before {
                            position: relative;
                            top: 2px;
                            left: 2px;
                        }
                    }

                    .text {
                        display: block;
                        padding-left: 32px;
                    }
                
                    label {
                        color: $setting-description;
                        font-size: 12px;
                        line-height: 16px;
                    }
                }

                // Custom box controls
                .custom-box-controls {

                    label {
                        display: block;
                        margin-bottom: 6px;
                    }
                }

                // Generate Accessibility Statement
                button.generate-accessibility-statement {
                    display: flex;
                    flex-wrap: wrap;
                    padding: 12px 18px;
                    justify-content: center;
                    align-items: center;
                    gap: 6px;
                    border-radius: 8px;
                    border: 2px solid rgba(255, 255, 255, .12);
                    background: $primary-color;
                    box-shadow: 0 0 0 1px var(--Colors-Effects-Shadows-shadow-skeumorphic-inner-border, rgba(10, 13, 18, .18)) inset, 0 -2px 0 0 var(--Colors-Effects-Shadows-shadow-skeumorphic-inner, rgba(10, 13, 18, .05)) inset, 0 1px 2px 0 var(--Colors-Effects-Shadows-shadow-xs, rgba(10, 13, 18, .05));
                    color: $light-color;
                    font-size: 16px;
                    font-style: normal;
                    font-weight: 600;
                    line-height: 24px;
                    width: 100%;
                    margin-top: 0;

                    &:hover {
                        background: $primary-hover-color;
                        background: #023FDC;
                    }
                }

                .note {
                    position: absolute;
                    left: 28px;
                    margin-top: calc(24px + 18px);
                    padding-right: 58px;
                    width: calc(392px - 24px);
                    display: block;
                    color: $setting-description;
                    font-size: 14px;
                    font-style: normal;
                    font-weight: 400;
                    line-height: 20px;
                }
            }

            // Right
            .right-column {
                width: calc(100% - 404px - 48px);
                padding: 32px;
                border-radius: 16px;
                background: $light-bg;

                // Box information
                .box-information {

                    .setting-title {

                        @extend %setting-title;
                    }

                    .setting-description {

                        @extend %setting-description;
                    }
                }

                // Box editor
                .box-editor {

                    .wp-editor-wrap {

                        .wp-editor-container {
                            border: none;

                            .mce-toolbar-grp.mce-panel {
                                border: none;
                                background: none;
                                margin-bottom: 24px;
                            }

                            .mce-top-part {

                                &::before {
                                    border: none;
                                    box-shadow: none;
                                }
                            }

                            .mce-edit-area {
                                outline: 1px solid $border-secondary-color;
                                border-radius: 8px;
                                padding: 12px 14px;
                                margin-bottom: 0;
                            }

                            .mce-resizehandle,
                            .mce-path.mce-flow-layout-item.mce-first {
                                display: none;
                            }

                            .mce-statusbar.mce-container.mce-panel.mce-stack-layout-item.mce-last {
                                border: none;
                            }
                        }
                    }
                }
            }
        }
    }

    p.submit {
        display: none;
    }
}

@media only screen and (max-width: 1200px) {

    .wrap {

        form {

            .mycontainer {

                .left-column,
                .right-column {
                    width: 100%;
                    overflow: hidden;
                }

                .left-column {

                    .note {
                        position: static;
                        width: 100%;
                        margin: 15px 0 0 0;
                    }
                }
            }
        }
    }
}

@media only screen and (max-width: 768px) {

    .wrap {

        form {

            .mycontainer {
                gap: 15px;
                
                .left-column,
                .right-column {
                    padding: 15px;
                }

                .left-column {

                    .box-control-confirm {

                        input::before {
                            top: -2px;
                            left: -2px;
                        }
                    }
                }
            }
        }
    }
}