@use '../abstracts/placeholders/placeholders' as *;
@use '../abstracts/variables/colors' as *;
@use '../abstracts/variables/size' as *;

// Sweetalert.
.swal-modal {

    .swal-title {
        color: $dark-color;
    }

    .swal-text {
        text-align: center;
        padding: 0 10%;
    }

    .swal-icon.swal-icon--info {
        border-color: #f1c40f;
        width: 60px;
        height: 60px;
        margin-bottom: 5px;
    }

    .swal-icon--info::before,
    .swal-icon--info::after{
        background: #f1c40f;
    }

    .swal-icon--info::before {
        width: 4px;
        height: 23px;
        bottom: 10px;
    }

    .swal-icon--info::after {
        width: 6px;
        height: 6px;
        top: 14px;
    }

    button.swal-button {
        background: $primary-color;

        &:hover {
            background: $primary-color;
            opacity: 0.8;
        }
    }

    .swal-button {

        &:focus {
            box-shadow: none;
            outline: 2px solid $light-color;
            outline-offset: -4px;
        }
    }
} 

body.wp-admin {

    #wpfooter {
        display: none;
    }

    .wrap {
        box-sizing: border-box;
        background: $secondary-bg;
        padding: 0;
        margin: 0;

        a:focus,
        a:focus .media-icon img,
        a:focus .plugin-icon,
        .wp-person a:focus .gravatar {
            border: 1px solid $primary-hover-color;
            box-shadow: none;
            outline: none;
        }        

        .preview-viewport {
            display: none;

            &.active {
                display: block;
            }
        }

        .options-wrapper {
            padding: 0 28px;

            .setting-title {

                @extend %setting-title;
            }

            .setting-description {

                @extend %setting-description;
            }

            .control-heading {

                @extend %control-heading;
            }

            .save-changes {
                margin-top: 20px;
            }

            input,
            select {

                @extend %form-input;

                &:focus {
                    border: 1px solid $primary-color;
                    box-shadow: 0 0 0 1px $primary-color;
                }
            }

            select {
                background-position: right 12px top 55%;
                background-size: 16px 16px;
            }

            form {

                > h2 {
                    display: none;
                }

                table {
                    padding: 0;
                    margin: 0;

                    tr {

                        th[scope="row"] {
                            display: none;
                        }

                        td  {
                            padding: 0;
                            margin: 0;

                            .settings-group {

                                &.hide-setting-description {

                                    .setting-title {
                                        padding-bottom: 20px;
                                        margin-bottom: 20px;
                                        border-bottom: 1px solid $border-color-light;
                                    }
                                }

                                .setting-control {
                                    background: $light-bg;
                                    padding: 0 32px;
                                }

                                .first-control {
                                    border-radius: 16px 16px 0 0;
                                    background: $light-bg;
                                    padding: 32px;
                                    padding-bottom: 0;

                                    .control-heading {
                                        padding-top: 0;
                                    }
                                }

                                .last-control {
                                    border-radius: 0 0 16px 16px;
                                    background: $light-bg;
                                    padding: 32px;
                                    padding-top: 0;
                                    margin-bottom: 24px;

                                    .boxes {

                                        .box {
                                            border: none;
                                        }
                                    }
                                }

                                .first-control.last-control {
                                    border-radius: 16px;
                                    padding: 32px;
                                }

                                &.device {
                                    display: none;
                                }

                                &.active {
                                    display: block;
                                }
                            }
                        }

                        &.submit {
                            display: none;
                        }
                    }
                }
            }
        }
    }
}

@media only screen and (max-width: 768px) {

    body.wp-admin {

        .wrap {

            .options-wrapper {
                padding: 0;

                form {

                    table {

                        tr {

                            td {

                                .settings-group {

                                    &.hide-setting-description {

                                        .setting-title {
                                            padding-bottom: 15px;
                                            margin-bottom: 15px;
                                            border-bottom: 1px solid $border-color-light;
                                        }
                                    }

                                    .setting-control {
                                        padding: 0 15px 15px 15px;
    
                                        &.first-control {
                                            padding-top: 15px;
                                        }

                                        &.last-control {
                                            margin-bottom: 15px;
                                        }

                                        &.first-control.last-control {
                                            padding: 15px;
                                        }
    
                                        .setting-description {
                                            padding-bottom: 15px;
                                            margin-bottom: 15px;
                                        }
    
                                        .control-heading {
                                            padding: 0 0 15px 0;
                                        }
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}