@use '../abstracts/placeholders/placeholders' as *;
@use '../abstracts/variables/colors' as *;
@use '../abstracts/variables/size' as *;

/* stylelint-disable selector-id-pattern */
#apop_modules,
#onetap_modules {

    .settings-group.accessibility-profiles,
    .settings-group.highlight-links, 
    .settings-group.saturation,
    .settings-group.stop-animations  {

        .boxes {
    
            .box {
    
                padding-bottom: 0;
                border: none;
            }
        }
    }

    .settings-group.accessibility-profiles,
    .settings-group.bigger-text,
    .settings-group.invert-colors,
    .settings-group.reading-line {

        .setting-control {

            .setting-title {
                border-bottom: 1px solid $border-color-light;
                padding-bottom: 16px;
            }
        }
    } 
}
/* stylelint-enable selector-id-pattern */