@use '../../abstracts/variables/colors' as *;

.box-current-language {

    a.current-language {
        text-decoration: none;
        cursor: pointer;
        display: flex;
        flex-wrap: wrap;
        gap: 8px;
        justify-content: flex-start;
        align-items: center;
        padding: 8px;
        border-style: solid;
        border-width: 1px 1px 1px 1px;
        border-color: $border-color;
        border-radius: 0.5rem 0.5rem 0.5rem 0.5rem;

        .image {

            img {
                width: 14px;
                height: auto;
                display: none;

                &.active {
                    display: block;

                }
            }
        }

        .text {
            color: $control-text;
            font-weight: 400;
            font-size: 16px;
            margin: 0;

            strong {
                color: #000;
            }
        }
    }
}

@media only screen and (max-width: 1200px) {

    .box-current-language {

        a.current-language {
            flex-wrap: nowrap;
            align-items: flex-start;

            .image {
                position: relative;
                top: 6px;
            }
        }
    }
}