@use '../abstracts/variables/colors' as *;
@use '../abstracts/variables/size' as *;
@use '../abstracts/mixins/mixins' as *;

.setting-control.select {

    .boxes {

        .box {
            position: relative;

            .badge {
                display: inline-block;
                display: none;
                padding: 2px 6px;
                border-radius: 6px;
                border: 1px solid $border-secondary-color;
                background: $light-bg;
                color: $button-color;
                text-align: center;
                font-size: 12px;
                font-weight: 500;
                line-height: 18px;
                position: absolute;
                top: 13px;
                left: 160px;
            }
        }
    }
}
