@use '../abstracts/variables/colors' as *;
@use '../abstracts/variables/size' as *;
@use '../abstracts/mixins/mixins' as *;

.setting-control.color {

    .boxes {
        display: flex;
        flex-wrap: wrap;
        align-items: center;

        .box1 {

            .wp-picker-holder {

                .iris-picker{
                    margin-top: 12px;
                }
            }

            .color-picker-field,
            .wp-picker-clear {
                border-radius: 8px;
                border: 1px solid $border-secondary-color;
                background: $light-bg;
                box-shadow: 0 1px 2px 0 rgba(10, 13, 18, 0.05);
                padding: 8px 12px;
                margin: 0 12px;
                width: 111px;
                height: auto;
                text-align: center;
                line-height: 1.2;
            }

            .wp-picker-clear {
                margin-left: 0;
            }

            .wp-picker-container {

                button.wp-color-result {
                    margin: 0;
                    padding: 0 0 0 30px;
                    border: none;
                    border-radius: 100%;
                    width: 28px;
                    height: 28px;
                    outline: 2px solid var(--outline-color);
                    outline-offset: 2px;

                    .wp-color-result-text {
                        display: none;
                    }
                }

                &.wp-picker-active {

                    button.wp-color-result {
                        position: relative;
                        top: 2px;
                    }
                }
            }
        }

        .box2 {
            border-radius: 8px;
            border: 1px solid $border-secondary-color;
            background: $light-bg;
            box-shadow: 0 1px 2px 0 rgba(10, 13, 18, 0.05);
            padding: 8px 12px;
            padding-left: 8px;
            margin-left: 12px;
            margin-left: 5px;
            margin-right: 40px;
            cursor: pointer;
            width: 60px;
            text-align: center;

            .color-result {
                border: 1px solid $border-secondary-color;
                padding: 8px 12px;
                border-radius: 8px;
            }
        }

        .box3 {

            ul {
                padding: 0;
                margin: 0;
                display: flex;
                flex-wrap: wrap;
                gap: 8px;

                li {
                    width: 28px;
                    height: 28px;
                    border-radius: 100%;
                    border: 1px solid rgba(0, 0, 0, 0.10);
                    background: $setting-description;
                    margin: 0;
                    cursor: pointer;

                    &:hover {
                        outline: 2px solid $border-secondary-color;
                        outline-offset: 1px;
                    }
                }
            }
        }
    }
}

@media only screen and (max-width: 768px) {

    .setting-control.color {

        .boxes {
    
            .box3 {
                margin-top: 15px;
                width: 100%;
            }
        }
    }
}