@use '../abstracts/placeholders/placeholders' as *;
@use '../abstracts/variables/colors' as *;
@use '../abstracts/variables/size' as *;

/* stylelint-disable selector-id-pattern */
#apop_general_settings,
#onetap_general_settings {

    .settings-group.alt-text-ai {
        display: none;
    }    

    .settings-group.hide_powered_by_onetap,
    .settings-group.alt-text-ai {

        .setting-control {

            .setting-title {
                border-bottom: 1px solid $border-color-light;
                padding-bottom: 16px;
            }
        }
    } 
}
/* stylelint-enable selector-id-pattern */