@use '../abstracts/placeholders/placeholders' as *;
@use '../abstracts/variables/colors' as *;
@use '../abstracts/variables/size' as *;

.wrap {

    .settings-container {

        .settings-row {
            display: flex;
            flex-wrap: wrap;
            justify-content: space-between;

            .options-wrapper {
                width: calc(100% - 350px - 132px);

                .settings-group.size {

                    .boxes {

                        .box {

                            label {
                                display: flex;
                                flex-wrap: wrap;
                                align-items: center;
                                justify-content: center;
                                height: 67px;
                                width: 62px;
                            }
                        }

                        .box1 {

                            img {
                                height: 20px;
                                width: 20px;
                                padding: 10px;
                            }
                        }

                        .box2 {

                            img {
                                height: 30px;
                                width: 30px;
                                padding: 15px;
                            }
                        }

                        .box3 {

                            img {
                                height: 35px;
                                width: 35px;
                                padding: 17.5px;
                            }
                        }
                    }
                }

                .settings-group.border {

                    .boxes {

                        .box1 {

                            img {
                                border: solid 2px $light-color;
                                box-shadow: 0 0 0 4px var(--outline-color, #0048FE);
                            }
                        }
                    }
                }                

                .settings-group.position-top-bottom,
                .settings-group.position-top-bottom-tablet,
                .settings-group.position-top-bottom-mobile,
                .settings-group.position-left-right,
                .settings-group.position-left-right-tablet,
                .settings-group.position-left-right-mobile {

                    .setting-control {

                        .control-heading {
                            padding-bottom: 0;
                        }
                    }
                }
            }

            .sidebar-preview {
                display: none;
                position: sticky;
                position: -webkit-sticky;
                top: 140px;
                width: 350px;
                height: 412px;
                border-radius: 16px;
                margin-right: 28px;
                padding: 24px;
                background: #fdfdfd;

                .box-title {

                    h3 {
                        color: $setting-title;
                        font-size: 18px;
                        font-weight: 600;
                        line-height: 28px;
                        margin: 0;
                        margin-bottom: 22px;
                    }
                }

                .devices-tabs {

                    @extend %devices-tabs;
                    margin-bottom: 22px;
                }

                .preview-frame {

                    .preview-container {
                        height: 270px;
                        border-radius: 16px;
                        padding: 12px;
                        background: $secondary-bg;

                        .preview-viewport {
                            height: 100%;
                            width: 100%;
                            background-size: contain;
                            background-repeat: no-repeat;
                            background-position: center;
                            display: none;

                            &.active {
                                display: block;
                                transform: scale(0.27);
                                transform-origin: top left;
                                width: 1200px;
                                height: 1000px;
                            }

                            button {
                                right: 0;
                                margin-right: 20px;
                                margin-right: 40px;
                                bottom: 0;
                                margin-bottom: 20px;
                                margin-bottom: 40px;
                                z-index: 10000000000000000;
                                position: fixed;
                                cursor: pointer;
                                box-shadow: none;
                                border: 2px solid transparent;
                                background: 0 0;
                                padding: 0;
                                transition: transform .15s ease;
                                width: auto;
                                height: auto;

                                img {
                                    padding: 15px;
                                    width: 65px;
                                    height: 65px;
                                    background: #0048FE;
                                    border-radius: 50%;
                                    display: block;
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}

@media only screen and (max-width: 1200px) {

    .wrap {

        .settings-container {
    
            .settings-row {
                margin-bottom: 24px;
    
                .options-wrapper {
                    width: 100%;
                }
    
                .sidebar-preview {
                    position: unset;
                    margin: 0 28px;
                }
            }
        }
    }
}

@media only screen and (max-width: 768px) {

    .wrap {

        .settings-container {
    
            .settings-row {
                margin-bottom: 15px;

                .sidebar-preview {
                    padding: 15px;
                    margin: 0;
                    height: 398px;

                    .box-title {

                        h3 {
                            margin-bottom: 15px;
                        }
                    }

                    .devices-tabs {
                        margin-bottom: 15px;                        
                    }
                }
            }
        }
    }
}
