@use '../abstracts/variables/colors' as *;
@use '../abstracts/variables/size' as *;
@use '../abstracts/mixins/mixins' as *;

.setting-control.number-slider {

    .boxes {

        .box {
            display: flex;
            flex-wrap: wrap;
            align-items: center;
            gap: 24px;
        
            .box-range {
                width: calc(100% - 100px - 54px);
        
                input {
                    -webkit-appearance: none;
                    appearance: none;
                    width: 100%;
                    height: 8px;
                    background: linear-gradient(to right, $primary-color 20%, $border-color-light 20%);
                    border-radius: 9999px;
                    outline: 0;
                    cursor: pointer;

                    &[type="range"] {
                        padding: 0;
                        overflow: unset;
                        border: 0;
                    }
        
                    &::-webkit-slider-thumb {
                        -webkit-appearance: none;
                        appearance: none;
                        height: 24px;
                        width: 24px;
                        border-radius: 9999px;
                        border: 2px solid $primary-color;
                        background: $light-bg;
                        box-shadow: 0 4px 6px -1px rgba(10, 13, 18, 0.10), 0 2px 4px -2px rgba(10, 13, 18, 0.06);
                    }
        
                    &[type="range"]::-moz-range-thumb {
                        -webkit-appearance: none;
                        appearance: none;
                        height: 24px;
                        width: 24px;
                        border-radius: 9999px;
                        border: 2px solid $primary-color;
                        background: $light-bg;
                        box-shadow: 0 4px 6px -1px rgba(10, 13, 18, 0.10), 0 2px 4px -2px rgba(10, 13, 18, 0.06);
                    }

                    &:focus {
                        border: none;
                    }
                }
            }
        
            .box-number {
                border-radius: 8px;
                border: 1px solid $border-color-light;
                background: $light-bg;
                box-shadow: 0 1px 2px 0 rgba(10, 13, 18, 0.05);
                position: relative;
                padding: 10px 14px;
                width: 100px;
        
                input {
                    box-shadow: none;
                    border: none;
                    outline: none;
                    padding: 0;
                    margin: 0;
                    min-height: auto;
                    overflow: hidden;
                    color: $text-color;
                    text-overflow: ellipsis;
                    font-size: 16px;
                    font-style: normal;
                    font-weight: 400;
                    line-height: 24px;
                }
        
                input[type="number"]::-webkit-inner-spin-button,
                input[type="number"]::-webkit-outer-spin-button {
                    -webkit-appearance: none;
                    margin: 0;
                }
        
                input[type="number"] {
                    -moz-appearance: textfield;
                }
        
                .unit {
                    position: absolute;
                    right: 14px;
                    top: 10px;
                    display: block;
                    color: $setting-description;
                    font-size: 16px;
                    font-style: normal;
                    font-weight: 400;
                }
            }
        }
    }
}

@media only screen and (max-width: 768px) {

    .setting-control.number-slider {

        .boxes {

            .box {
                gap: 15px;

                .box-range {
                    width: calc(100% - 100px - 15px);
                }

                .box-number {
                    width: 70px;
                }
            }
        }
    }
}