@use '../abstracts/placeholders/placeholders' as *;
@use '../abstracts/variables/colors' as *;
@use '../abstracts/variables/size' as *;

.setting-control.copyable_text {

    .boxes {

        .box {
            display: flex;
            flex-wrap: wrap;
            align-items: center;
            justify-content: space-between;
            padding: 16px 0;
            border-bottom: 1px solid $border-color-light;

            .left {
                display: flex;
                flex-wrap: wrap;
                gap: 16px;

                .icon {
                    border-radius: 8px;
                    border: 1px solid $border-color-light;
                    background: $secondary-bg;
                    box-shadow: 0 1px 2px 0 rgba(10, 13, 18, 0.05);
                    width: 40px;
                    height: 40px;

                    img {
                        padding: 8px;
                    }
                }

                .text {
                    display: flex;
                    flex-wrap: wrap;
                    flex-direction: column;
                    gap: 2px;

                    span.feature-name {
                        color: $control-text;
                        font-size: 16px;
                        font-style: normal;
                        font-weight: 600;
                        line-height: 24px;
                        display: block;
                        position: relative;

                        .pro {
                            position: absolute;
                            padding: 2px 6px;
                            align-items: center;
                            border-radius: 6px;
                            border: 1px solid $border-color-light;
                            background: $secondary-bg;
                            color: $primary-color;
                            text-align: center;
                            font-size: 12px;
                            font-style: normal;
                            font-weight: 500;
                            line-height: 18px;
                            margin-left: 10px;
                            bottom: 0;
                        }
                    }

                    span.feature-desc {
                        overflow: hidden;
                        color: $control-text;
                        text-overflow: ellipsis;
                        font-size: 14px;
                        font-style: normal;
                        font-weight: 400;
                        line-height: $line-height;
                    }
                }
            }

            .right {
                position: relative;

                .pro-info {
                    display: none;
                    padding: 12px;
                    border-radius: 8px;
                    background: $dark-color;
                    position: absolute;
                    bottom: -19px;
                    right: 65px;
                    height: 38px;
                    width: 200px;                    

                    &::before {
                        content: "";
                        position: absolute;
                        top: 0;
                        right: -20px;
                        width: 50px;
                        height: 62px;
                    }
                
                    &::after {
                        content: "";
                        position: absolute;
                        top: 50%;
                        right: -10px;
                        transform: translateY(-50%);
                        width: 0;
                        height: 0;
                        border-left: 10px solid $dark-color;
                        border-top: 10px solid transparent;
                        border-bottom: 10px solid transparent;
                    }

                    .pro-title {
                        color: $light-color;
                        font-size: 12px;
                        font-style: normal;
                        font-weight: 600;
                        line-height: 18px;
                        margin: 0;
                        margin-bottom: 2px;
                    }

                    .pro-desc {
                        color: $border-secondary-color;
                        font-size: 12px;
                        font-style: normal;
                        font-weight: 500;
                        line-height: 18px;

                        a {
                            color: $border-secondary-color;
                            text-decoration: underline;
                        }
                    }
                }

                &:hover {

                    .pro-info {
                        display: block;
                    }
                }

                .box-copy-text {
                    display: flex;
                    flex-wrap: wrap;
                    justify-content: space-between;
                    align-items: center;
                    border-radius: 8px;
                    border: 1px solid $border-secondary-color;
                    background: $light-bg;
                    overflow: hidden;
                    color: $text-color;
                    text-overflow: ellipsis;
                    font-size: 16px;
                    line-height: 24px;
                    min-width: 300px;

                    .copy-text {
                        padding: 8px 12px;
                    }

                    button {
                        cursor: pointer;
                        border: none;
                        border-left: 1px solid $border-secondary-color;
                        background: $light-bg;
                        display: flex;
                        flex-wrap: wrap;
                        gap: 6px;
                        padding: 10px 14px;
                        color: $button-color;
                        font-size: 14px;
                        font-weight: 600;
                        line-height: 20px;
                    }
                }
            }
        }
    }
}

@media only screen and (max-width: 768px) {

    .setting-control.copyable_text {

        .boxes {

            .box {
                gap: 15px;
                padding-top: 0;
            }
        }
    }
}