@use '../abstracts/variables/colors' as *;
@use '../abstracts/variables/size' as *;
@use '../abstracts/mixins/mixins' as *;

.settings-group.license {
    position: relative;

    .license-status-label {
        display: none;
        padding: 2px 6px;
        border-radius: 6px;
        text-align: center;
        font-size: 12px;
        font-style: normal;
        font-weight: 500;
        line-height: 18px;
        position: relative;
        left: 8px;
        bottom: 4px;
    }

    .license-status-label[license-status="0"] {
        border: 1px solid #FECDCA;
        background: #FEF3F2;
        color: #B42318;
    }

    .license-status-label[license-status="1"] {
        border: 1px solid #ABEFC6;
        background: #ECFDF3;
        color: #067647;
    }

    .setting-control {

        .setting-title {

            .license-status-label {
                display: inline-block;
            }
        }
    }

    .boxes {

        .box {

            input {
                font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen-Sans, Ubuntu, Cantarell, "Helvetica Neue", sans-serif;
            }            

            input[readonly] {
                background: $light-bg;
                cursor: not-allowed;
            }
        }
    }
}

@media only screen and (max-width: 768px) {

    .settings-group.license {
    
        .boxes {
    
            .box {
    
                .license-status-label {
                    top: 16px;
                    left: 125px;
                }
            }
        }
    }
}
