@use '../abstracts/placeholders/placeholders' as *;
@use '../abstracts/variables/colors' as *;
@use '../abstracts/variables/size' as *;

/* stylelint-disable selector-id-pattern */
#apop_module_labels,
#onetap_module_labels {

    .settings-group.bigger-text {

        .setting-control {

            .setting-title {
                display: flex;
                flex-wrap: wrap;
                align-items: center;
                justify-content: space-between;
                gap: 15px;
            }
        }
    }     

    .settings-group.bigger-text, 
    .settings-group.invert-colors,
    .settings-group.reading-line {

        .setting-control {

            .setting-title {
                border-bottom: 1px solid $border-color-light;
                padding-bottom: 16px;
            }
        }
    } 

    .settings-group {

        .setting-control {

            &.module-labels {

                .boxes {

                    .box {

                        &:hover {

                            .left {

                                .text {

                                    .feature-name {
                                        text-decoration: underline;
                                    }
                                }
                            }
                        }

                        .left {

                            .text {

                                span.feature-desc {
                                    display: none;
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}
/* stylelint-enable selector-id-pattern */