@use '../abstracts/variables/colors' as *;
@use '../abstracts/variables/size' as *;
@use '../abstracts/mixins/mixins' as *;

.setting-control.divider {

    .boxes {

        .box {
            padding: 20px 0;

            .divider {
                height: 1px;
                background: $border-color-light;
            }
        }
    }
}

@media only screen and (max-width: 768px) {

    .setting-control.divider {

        .boxes {
    
            .box {
                padding: 15px 0;
            }
        }
    }
}