@use '../../abstracts/variables/colors' as *;
@use '../../abstracts/variables/size' as *;

/**
 * Toggle Switch Placeholder
 * Provides consistent styling for toggle switch components
 */
%toggle-switch {
    display: inline-block;
    width: 44px;
    height: 24px;
    padding: 2px;

    input {
        display: none;
    }

    .slider {
        position: absolute;
        cursor: pointer;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background: $border-color;
        -webkit-transition: 0.4s;
        transition: 0.4s;
        margin: 0;
    }

    .slider::before {
        content: "";
        position: absolute;
        left: 3px;
        bottom: 4px;
        -webkit-transition: .4s;
        transition: .4s;
        height: 20px;
        width: 20px;
        border-radius: 9999px;
        background: $light-bg;
    }

    input:checked + .slider {
        background: $primary-color;
        border-radius: 50px;
    }

    input:focus + .slider {
        border-radius: 50px;
    }

    input:checked + .slider::before {
        -webkit-transform: translateX(22px);
        -ms-transform: translateX(22px);
        transform: translateX(22px);
    }

    .slider.round {
        border-radius: 50px;
        width: 48px;
    }
}

/**
 * Form Label Placeholder  
 * Provides consistent styling for form labels (more prominent than setting labels)
 * Usage: @extend %form-label;
 */
%form-label {
    color: $control-text;
    font-size: 14px;
    font-style: normal;
    font-weight: 500;
    line-height: $line-height;
}

/**
 * Form Input Placeholder
 * Provides consistent styling for form input fields
 * Usage: @extend %form-input;
 */
%form-input {
    padding: 12px;
    border-radius: 8px;
    border: 1px solid $border-secondary-color;
    box-shadow: 0 1px 2px 0 rgba(10, 13, 18, .05);
    overflow: hidden;
    color: $text-color;
    text-overflow: ellipsis;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 24px;
    width: 100%;
    max-width: 100%;
    height: auto;
    min-height: auto;
}

/**
 * Setting Title Placeholder
 * Provides consistent styling for setting section titles
 * Usage: @extend %setting-title;
 */
%setting-title {
    color: $setting-title;
    font-size: 18px;
    font-style: normal;
    font-weight: 600;
    line-height: 28px;
    display: block;
}

/**
 * Setting Description Placeholder
 * Provides consistent styling for setting descriptions with bottom border
 * Usage: @extend %setting-description;
 */
%setting-description {
    overflow: hidden;
    color: $setting-description;
    text-overflow: ellipsis;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: $line-height;
    display: block;
    padding-bottom: 20px;
    margin-bottom: 20px;
    border-bottom: 1px solid $border-color-light;

    // Link styling within description
    a {
        color: $primary-color;
    }
}

/**
 * Control Heading Placeholder
 * Provides consistent styling for control section headings
 * Usage: @extend %control-heading;
 */
%control-heading {
    font-size: 14px;
    font-style: normal;
    line-height: $line-height;
    padding: 20px 0;
    display: block;
    font-weight: 500;
    color: $setting-title;
}

/**
 * Button Primary Placeholder
 * Provides consistent styling for primary button components
 * Usage: @extend %button-primary;
 */
%button-primary {
    cursor: pointer;
    border: 1px solid $border-secondary-color;
    background: $light-bg;
    border-radius: 8px;
    display: flex;
    flex-wrap: wrap;
    gap: 6px;
    padding: 10px 16px;
    color: $button-color;
    font-size: 14px;
    font-weight: 600;
    line-height: 20px;
    text-decoration: none;
}

%devices-tabs {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 4px;
    gap: 4px;
    border-radius: 10px;
    border: 1px solid $border-color-light;
    background: $device-switcher-bg;

    button {
        display: flex;
        height: 36px;
        padding: 8px 12px;
        justify-content: center;
        align-items: center;
        gap: 8px;
        border-radius: 6px;
        background: $device-switcher-bg;
        border: none;
        width: 33%;
        cursor: pointer;

        font-size: 14px;
        font-style: normal;
        line-height: $line-height;
        font-weight: 500;
        color: $setting-title;

        &:hover,
        &.active {
            background: $light-bg;
            box-shadow: 0 1px 3px 0 var(--Colors-Effects-Shadows-shadow-sm_01, rgba(10, 13, 18, 0.10)), 0 1px 2px -1px var(--Colors-Effects-Shadows-shadow-sm_02, rgba(10, 13, 18, 0.10));
        }
    }

    @media only screen and (max-width: 480px) {

        button {
            gap: 4px;
        }
    }
}
