@use '../../abstracts/variables/colors' as *;
@use '../../abstracts/variables/size' as *;
@use '../../abstracts/mixins/mixins' as *;

.save-changes {
    cursor: pointer;
    border-radius: 8px;
    border: 2px solid rgba(255, 255, 255, 0.12);
    border: none;
    background: $primary-color;
    color: $light-color;
    font-size: 14px;
    font-weight: 600;
    line-height: $line-height;
    outline: none;
    padding: 10px 16px;
    margin: 0;
    // box-shadow: 0 0 0 1px rgba(10, 13, 18, 0.18) inset, 0 -2px 0 0 rgba(10, 13, 18, 0.05) inset, 0 1px 2px 0 rgba(10, 13, 18, 0.05);

    &:hover {
		background: $primary-hover-color;
		background: #023FDC;
	}

  &.license-active {
    background-color: #e3e9f1;
    color: #596575;

    &:hover {
      background: #023fdc;
      color: $light-color;
    }
  }
}