@use '../abstracts/variables/colors' as *;
@use '../abstracts/variables/size' as *;
@use '../abstracts/mixins/mixins' as *;

.setting-control.radio-text {

    .boxes {
        display: flex;
        flex-wrap: wrap;
        justify-content: flex-start;
        gap: 20px;

        .box {

            label {
                border-radius: 8px;
                border: 1px solid $border-secondary-color;
                background: $light-bg;
                padding: 8px 14px;
                display: block;   
                cursor: pointer;     
                
                &.checked {
                    border: 1px solid $primary-color;
                    box-shadow: 0 0 0 1px $primary-color;
                }

                .text {
                    color: $control-text;
                    font-size: 14px;
                    font-style: normal;
                    font-weight: 600;
                    line-height: $line-height;
                    display: block;
                }

                input {
                    display: none;
                }
            }
        }
    }
}

@media only screen and (max-width: 768px) {

    .setting-control.radio-text {

        .boxes {
            gap: 15px;
        }
    }
}