@use '../abstracts/variables/colors' as *;
@use '../abstracts/variables/size' as *;
@use '../abstracts/mixins/mixins' as *;

.setting-control.radio-image {

    .boxes {
        display: flex;
        flex-wrap: wrap;
        justify-content: flex-start;
        gap: 20px;

        .box {

            label {
                border-radius: 10px;
                border: 1px solid $border-secondary-color;
                outline: none;
                background: $light-bg;
                padding: 28px;
                display: block;   
                text-align: center;
                cursor: pointer;     
                position: relative;

                &:hover {
                    box-shadow: 0 0 0 1px $border-secondary-color;
                }
                
                &.checked {
                    border: 1px solid $primary-color;
                    box-shadow: 0 0 0 1px $primary-color;

                    svg {
                        display: block;
                        position: absolute;
                        top: 7px;
                        right: 7px;
                    }
                }

                img {
                    border: 1px solid $border-secondary-color;
                    background: $primary-color;
                    height: 30px;
                    width: 30px;
                    border-radius: 100%;
                    padding: 15px;
                }

                input {
                    display: none;
                }

                svg {
                    display: none;
                }
            }
        }
    }
}

@media only screen and (max-width: 768px) {

    .setting-control.radio-image {

        .boxes {
            gap: 15px;
            justify-content: space-between;

            .box {
                width: calc(50% - 7.5px);
            }
        }
    }
}