@use '../abstracts/placeholders/placeholders' as *;
@use '../abstracts/variables/colors' as *;
@use '../abstracts/variables/size' as *;

/* stylelint-disable selector-id-pattern */
#apop_alt_text,
#onetap_alt_text {

    .setting-title {
        border-bottom: 1px solid $border-color-light;
        padding-bottom: 20px;
        margin-bottom: 20px;
    }
}
/* stylelint-enable selector-id-pattern */

.wrap {

    .box-alt-pilot {
        display: flex;
        flex-wrap: wrap;
        padding: 0 28px;
        margin-bottom: 34px;

        .mycontainer {
            width: 100%;

            .myrow {
                display: flex;
                flex-wrap: wrap;
                justify-content: space-between;
                align-items: center;
                gap: 15px;
                border-radius: 12px;
                border: 1px solid $primary-color;
                background: #023FDC;
                padding: 12px;

                .logo {
                    display: flex;
                    flex-wrap: wrap;
                    align-items: center;

                    span {
                        color: $light-color;
                        font-size: 16px;
                        font-weight: 600;
                        line-height: 24px;

                        &.save-hours {
                            margin-left: 12px;
                            margin-right: 3px;
                            text-decoration: underline;
                        }
        
                        &.try-free {
                            color: #D9EAFF;
                            font-weight: 400;
                        }
                    }
                }
        
                .link {
                    background: $light-bg;
                    border-radius: 8px;
                    border: 1px solid $border-secondary-color;
                    padding: 10px 18px;

                    a {
                        color: $button-color;
                        font-size: 16px;
                        font-weight: 600;
                        line-height: 24px;
                        text-decoration: none;
                    }
                }
            }
        }
    }

    .box-image-alt {

        // Header & Body
        .table-header,
        .table-body {

            .row {

                .col.thumbnail {
                    width: 80px;
                    flex-grow: 1;
                }

                .col.alt-text {
                    width: 250px;
                    flex-grow: 5;
                    padding-right: 200px;
                }

                .col.uploaded {
                    width: 300px;
                    flex-grow: 3;
                }

                .col.actions {
                    display: flex;
                    justify-content: right;
                    flex-grow: 1;
                    width: 100px;
                    text-align: right;
                }
            }
        }

        // Header
        .table-header {

            .row {
                display: flex;
                border-bottom: 1px solid $border-color-light;

                .table-heading {
                    padding: 14px 26px;
                    padding-left: 0;
                    color: $text-color;
                    font-size: 14px;
                    font-weight: 600;
                    line-height: $line-height;
                }
            }
        }

        // Body
        .table-body {
            color: $setting-description;
            font-size: 14px;
            line-height: $line-height;
            text-align: left;

            .row {
                display: flex;
                border-bottom: 1px solid $border-color-light;

                .col {
                    display: flex;
                    flex-wrap: wrap;
                    align-items: center;
                    padding: 8px 26px;
                    padding-left: 0;

                    &.thumbnail {
                        background: $light-bg;

                        .image-thumbnail {
                            width: 65px;
                            height: 65px;
                            border-radius: 8px;
                            border: 1px solid rgba(0, 0, 0, 0.08);
                        }
                    }
    
                    &.alt-text {

                        textarea {
                            padding: 12px 14px;
                            border-radius: 8px;
                            border: 1px solid $border-secondary-color;
                            box-shadow: 0 1px 2px 0 var(--Colors-Effects-Shadows-shadow-xs, rgba(10, 13, 18, 0.05));
                            color: $text-color;
                            font-size: 14px;
                            line-height: $line-height;
                            min-height: 56px;
                            width: 100%;
                        }
                    }
        
                    &.actions {

                        .action-buttons {
                            display: flex;
                            align-items: center;
                            border-radius: 6px;
                            border: 1px solid $border-secondary-color;

                            a,
                            button {
                                display: flex;
                                align-items: center;
                                border: none;
                                padding: 8px 9px;

                                &:hover {
                                    background: none;
                                }

                                &:focus {
                                    background: none;
                                    outline: none;
                                    box-shadow: none;
                                }
             
                            }

                            a {
                                border-right: 1px solid $border-secondary-color;
                                border-radius: 0;
                            }

                            .ai-btn:hover {
                                background: $border-color-light;
                                border-radius: 5px 0 0 5px;
                            }

                            .edit-btn:hover,
                            .save-btn:hover {
                                background: $border-color-light;
                                border-radius: 0 5px 5px 0;
                            }
                        }
                    }
                }
            }
        }
    }

    .box-navigation {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        align-items: center;
        margin-top: 22px;

        .col.prev,
        .col.next {

            .button {
                display: flex;
                flex-wrap: wrap;
                align-items: center;
                gap: 7px;
                color: $button-color;
                border-radius: 8px;
                border: 1px solid $border-color-light;
                box-shadow: 0 1.115px 2.23px 0 rgba(10, 13, 18, 0.05);
                font-size: 15;
                font-style: normal;
                font-weight: 600;
                line-height: 22.295px;

                &:hover {
                    background: $light-bg;
                    color: $button-color;
                }
                
                &.disable {
                    color: #A4A7AE;
                }
            }
        }

        .col.links {

            ul {
                display: flex;
                flex-wrap: wrap;
                align-items: center;
                padding: 0;
                margin: 0;

                li {

                    a {
                        display: flex;
                        flex-wrap: wrap;
                        justify-content: center;
                        align-items: center;
                        width: 44px;
                        padding: 10px 0;
                        text-decoration: none;
                        color: $text-color;
                        text-align: center;
                        font-size: 15px;
                        font-style: normal;
                        font-weight: 500;
                        line-height: 22.295px;

                        &:focus {
                            box-shadow: none;
                            outline: none;
                        }

                        &.current {
                            border-radius: 8px;
                            background: $secondary-bg;
                            color: $button-color;
                        }
                    }
                }
            }
        }
    }
}

@media only screen and (max-width: 768px) {

    .wrap {

        .box-alt-pilot {
            padding: 0 15px;
            margin-bottom: 15px;
        }

        .box-image-alt {

            .table-header {

                .row {

                    .col.thumbnail,
                    .col.alt-text,
                    .col.uploaded,
                    .col.actions {
                        padding-right: 15px;
                    }

                    .col.thumbnail {
                        width: auto;
                        max-width: 80px;
                    }

                    .col.alt-text {
                        width: 300px;
                        padding-right: 30px;
                    }

                    .col.uploaded {
                        width: 150px;
                    }

                    .col.actions {
                        text-align: left;
                        justify-content: flex-start;
                        width: auto;
                        padding-right: 0;
                    }
                }
            }
        }
    }

    /* stylelint-disable selector-id-pattern */
    #apop_alt_text,
    #onetap_alt_text {

        .setting-title {
            padding-bottom: 15px;
            margin-bottom: 15px;
        } 
    }
    /* stylelint-enable selector-id-pattern */
}
